import subTopics1 from "../SubTopics/SubTopics1";
import "./SpmTopics.css";
const topics = [
  {
    id: 1,
    heading: "SPM",
    data: (
      <>
        {subTopics1.map((val) => {
          return (
            <>
              <div className="spmcard-2">
                <b>{val.title}</b>
                <p>{val.descp}</p>
              </div>
            </>
          );
        })}
      </>
    ),
  },
];
export default topics;
